class ArrayVolume {
  constructor (length) {
    this.queue = length === void 0 ? [] : new Array(length)
  }
  /**
   * 
   * @param { Function } callback 
   * @param { number } priority 
   * @returns { Function } remove 
   */
  push (callback, priority = 0) {
    if (priority === 0) {
      this.queue.push(callback)
    }
    if (priority === 1) {
      this.queue.unshift(callback)
    }
    return () =>
      this.queue.splice(
        this.queue.findIndex(fn => fn === callback),
        1
      )
  }
}

/**
 * @template T
 */
export class ChainRunner extends ArrayVolume {
  constructor () {
    super()
  }
  /**
   * @description 
   * @param { IGenericFunction<T,T> } callback 
   * @param { number } [priority=1] 
   * @returns { cancelCallback } remove 
   */
  push (callback, priority = 1) {
    return super.push(callback, priority)
  }
  /**
   * @description 
   * @param { IGenericFunction<T,T> } callback 
   * @returns { cancelCallback } remove 
   */
  use (callback) {
    return this.push(callback)
  }
  /**
   * @description 
   * @param  {*} result 
   * @returns {T}
   */
  run (result) {
    let res = result
    for (const fn of this.queue) {
      res = fn(res)
    }
    return res
  }
}
